@import url(https://fonts.googleapis.com/css2?family=Open+Sans&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Cairo:wght@300&family=Staatliches&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Staatliches&display=swap);
html,
body {
    min-height: 100vh;
}

body {
    margin: 0;
}

#root {
    min-height: 100vh;
}

.PageContent {
    display: flex;
    justify-content: center;
    align-items: center;
    min-height: 100vh;
    position: relative;
}

.DownButtonCont {
    position: fixed;
    z-index: 6;
    max-height: 7vh;
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    margin-right: 7.5px;
    bottom: 0;
    -webkit-transform: rotate(90deg) scaleX(1);
            transform: rotate(90deg) scaleX(1);
}

.UpButtonCont {
    position: fixed;
    z-index: 6;
    max-height: 7vh;
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    margin-right: 7.5px;
    top: 0;
    -webkit-transform: rotate(90deg) scaleX(1);
            transform: rotate(90deg) scaleX(1);
}

.ChangeButton {
    font-size: 100px;
    background-color: rgba(0, 0, 0, 0.6);
    color: #fff;
    font-family: Staatliches;
    height: 40vh;
    border: none;
    border-radius: 5px;
}

.ChangeButton:hover {
    background-color: rgba(0, 0, 0, 0.9);
}

.ButtonText {
    color: white;
    opacity: 1;
}

.AboutMeParentCont {
    min-width: 100%;
    height: 0vh;
    background-color: rgb(178, 223, 178);
    position: fixed;
    bottom: 0;
    z-index: 3;
}

.AboutMeParentCont.active {
    height: 100vh;
    -webkit-animation-name: showFrom;
            animation-name: showFrom;
    -webkit-animation-duration: 2s;
            animation-duration: 2s;
}

.AboutMeParentCont.unactive {
    height: 0vh;
    -webkit-animation-name: lower;
            animation-name: lower;
    -webkit-animation-duration: 2s;
            animation-duration: 2s;
}

@-webkit-keyframes showFrom {
    from {
        height: 0vh;
    }
    to {
        height: 100vh;
    }
}

@keyframes showFrom {
    from {
        height: 0vh;
    }
    to {
        height: 100vh;
    }
}

@-webkit-keyframes lower {
    from {
        height: 100vh;
    }
    to {
        height: 0vh;
    }
}

@keyframes lower {
    from {
        height: 100vh;
    }
    to {
        height: 0vh;
    }
}

.HomeDivParent {
    min-width: 100%;
    min-height: 100vh;
    display: flex;
    justify-content: center;
    position: fixed;
    z-index: 0;
}

.HomeCanvas {
    position: fixed;
    z-index: 0;
    top: 0;
    left: 0;
    overflow: hidden;
    background-color: black;
    min-height: 100vh;
    min-width: 100%;
}

.HomeInfoCont {
    display: flex;
    flex-direction: column;
    min-height: 100vh;
    min-width: 100%;
    justify-content: center;
    align-items: center;
    position: fixed;
    z-index: 1;
}

.HomeName {
    font-size: 148px;
    font-family: Staatliches;
    color: white;
    border-bottom: 15px solid white;
    margin-bottom: 2%;
}

.HomeLinkCont {
    display: flex;
    width: 100%;
    justify-content: space-evenly;
    align-items: center;
    margin-bottom: 1.3%;
}

.HomeLink {
    font-size: 100px;
    font-family: Staatliches;
    cursor: pointer;
    /* text-decoration: underline; */
    color: white;
}

.HomeLink:hover {
    text-decoration: underline;
    color: #62bcb9;
}

.ProjectsParentCont {
    min-width: 100%;
    height: 0vh;
    background-color: rgb(240, 122, 234);
    position: fixed;
    bottom: 0;
    z-index: 5;
}

.ProjectsParentCont.active {
    height: 100vh;
    -webkit-animation-name: raise;
            animation-name: raise;
    -webkit-animation-duration: 2s;
            animation-duration: 2s;
}

.ProjectsParentCont.unactive {
    height: 0vh;
    -webkit-animation-name: lower;
            animation-name: lower;
    -webkit-animation-duration: 2s;
            animation-duration: 2s;
}

@-webkit-keyframes raise {
    from {
        height: 0vh;
    }
    to {
        height: 100vh;
    }
}

@keyframes raise {
    from {
        height: 0vh;
    }
    to {
        height: 100vh;
    }
}

@-webkit-keyframes lower {
    from {
        height: 100vh;
    }
    to {
        height: 0vh;
    }
}

@keyframes lower {
    from {
        height: 100vh;
    }
    to {
        height: 0vh;
    }
}

