.AboutMeParentCont {
    min-width: 100%;
    height: 0vh;
    background-color: rgb(178, 223, 178);
    position: fixed;
    bottom: 0;
    z-index: 3;
}

.AboutMeParentCont.active {
    height: 100vh;
    animation-name: showFrom;
    animation-duration: 2s;
}

.AboutMeParentCont.unactive {
    height: 0vh;
    animation-name: lower;
    animation-duration: 2s;
}

@keyframes showFrom {
    from {
        height: 0vh;
    }
    to {
        height: 100vh;
    }
}

@keyframes lower {
    from {
        height: 100vh;
    }
    to {
        height: 0vh;
    }
}
